<template>
  <div id="app">
    <keep-alive>
        <router-view v-if='$route.meta.keepAlive'></router-view>
    </keep-alive>
        <router-view v-if='!$route.meta.keepAlive'></router-view>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
body{
    font-size: 0.12rem;
}
</style>



