<template>
  <div class="wrapper">
      <button class="gg" @click="logout()">退出</button>
      <img src="../assets/wx.png" alt="">
  </div>
</template>

<script>
export default {
  components:{},
  props:{},
  data(){
    return {
    }
  },
  watch:{},
  computed:{},
  methods:{
      logout(){

           localStorage.removeItem("token");
           this.$router.push("/login")
      }
  },
  created(){},
  mounted(){}
}
</script>
<style lang="less">
*{
  margin: 0;
  padding: 0;
}
.wrapper{
  width: 100%;
  height: 100%;
  // background-image: url(../assets/wx.png);
  // background-size: 100 100;
}
img{
  width: 100%;
  height: 100%;

}
.gg{
  position:absolute;
  top: .76rem;
  right: 2.8rem;
  width: .8rem;
  height: .5rem;
  font-size: .15rem;
  text-align: center;
  line-height: .3rem;
}

  
</style>


